import React from "react";
import paypalImg from "../assets/img/paypal.png";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import TimePicker from "react-time-picker";
import Popup from "reactjs-popup";
// import PaypalButton from './PaypalButton';
import PaypalExpressBtn from "react-paypal-express-checkout";
// import {getallclick} from '../actions/BoostContentAction';
import { connect } from "react-redux";
const Client = {
  sandbox: "AZBphGFQqiwEe8-Tjq1iv33xLnqbhwgcn_Tl8vjhOpE7KffDvVyW2AmO5Qmiv_6gn_LnQyi-pINAgcxl",
  production: "aa",
};
 
class BoostContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      startDate: new Date(),
      time: new Date(),
      user:[],
      user2:[],
      boostcontent:this.props.boostcontent,
      dropdown: false,
      selectedValue: {
        clickCount:0,amount:0
      }
      // no_of_click:this.props.boostcontent[0].no_of_clicks,
    };
  }
  componentDidMount() {
    if(localStorage.getItem('token') !== null ) {
        let user  = JSON.parse(localStorage.getItem('user'));
        let first = this.props.boostcontent && this.props.boostcontent.length>0 ? this.props.boostcontent:[]
        this.setState({
          user:user.name,
          user2:user.email,

        })
        if(first.length>0) {
          this.setState({
            selectedValue: {
              clickCount:first[0].no_of_clicks,amount:first[0].amount
            }
          })
        }
    } else {
       // window.location.href="/"
    }
  }

  // componentWillUnmount(){
  //   console.log('checking unmount')
  // }

  handleClickEvent = () =>{
    this.setState({dropdown: !this.state.dropdown})
  }
  handleChange = (date) => {
    this.setState({
      startDate: date,
    });
  };
  onChange = (time) => this.setState({ time });
  hanleramountchange = (e) => {
    let click;
    console.log(this.state.boostcontent)
    this.state.boostcontent.forEach(item=>{
      if(item.amount===e.target.value){
        click = item.no_of_clicks
      }
    })
    console.log(click)
    this.setState({no_of_click:click})
  }
  setInitialValue = (clickcount,amount) =>{
    this.setState({
      selectedValue:{
        clickCount:clickcount,
        amount:amount,
      },
      dropdown:false
    })
  }
  render() {
    const { user } = this.state;
    const { user2 } = this.state;
    console.log("valuesssssss",this.state.boostcontent)
    console.log("valuesssssss",this.props.boostcontent)
    return (
  <div className="overlay">
        <div className="boost-popup">
        <button className="btn-close" 
         onClick={e=> {this.props.isOpenF()}
      }>x</button>
          <form>
            <h2>Boost content</h2>
           <p>{this.props.title || ''}</p>
            <div className="form-group">
              <label className="label">
                <span>Boosted by</span>
              </label>
              <input
                type="text"
                value={user}
                className="input-field"
              />
            </div>

          <div class="form-group">
            <label className="label">
              <span>Click count</span>
            </label>
            <div class="select-wrap input-field" onClick={e =>this.handleClickEvent()}>
              { 
               this.state.selectedValue ?
              <><span class="selected-value">
              {this.state.selectedValue.clickCount}
              </span>
              <span class="dollor"> ${this.state.selectedValue.amount}</span></> : null}
            </div>
            <div class="select-list-wrap">
            <ul class={`select-list ${this.state.dropdown? '':'select-act'}`}>
            {
              this.state.boostcontent && this.state.dropdown ?
                this.state.boostcontent.map((item,i)=>{
                  return (<>                
                  <li class="list-item" onClick={e=>this.setInitialValue(item.no_of_clicks,item.amount)}>
                  <span>{item.no_of_clicks}</span>
                  <span>${item.amount}</span>

                </li>
                  </>)
                }) : null 
            }

            {/* <li class="list-item">
            <span>abc</span>
              <span>$50</span>
            </li> */}
          </ul>
            </div>
            </div>

        <div className="form-group time-picker">
          <label className="label">
            <span>Starting Time</span>
          </label>
          <TimePicker onChange={this.onChange} value={this.state.time} />
          <svg
            className="icon-clock"
            width="22"
            height="22"
            viewBox="0 0 22 22"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M11 22C17.0751 22 22 17.0751 22 11C22 4.92487 17.0751 0 11 0C4.92487 0 0 4.92487 0 11C0 17.0751 4.92487 22 11 22Z"
              fill="#D8D6D6"
            />
            <path
              d="M11 6.4707V12.2942L14.2353 15.5295"
              stroke="white"
              stroke-width="1.3"
              stroke-linecap="round"
            />
          </svg>
        </div>
        <div className="form-group date-picker">
          <label className="label">
            <span>Starting Date</span>
          </label>
          <DatePicker
            selected={this.state.startDate}
            onChange={this.handleChange}
          />
          <svg
            className="icon-cal"
            width="16"
            height="16"
            viewBox="0 0 16 16"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M15.0406 1.1875H13.4688V3.43125C13.4688 4.16563 12.9062 4.75 12.2 4.75H11.8C11.0938 4.75 10.5312 4.1625 10.5312 3.43125V1.1875H5.71875V3.43125C5.71875 4.16563 5.14688 4.75 4.44063 4.75H4.04063C3.33438 4.75 2.75 4.1625 2.75 3.43125V1.1875H0.959375C0.43125 1.1875 0 1.65 0 2.19688V15.0031C0 15.55 0.43125 16 0.959375 16H15.0406C15.5687 16 16 15.55 16 15.0031V2.19688C16 1.65 15.5687 1.1875 15.0406 1.1875ZM14.6562 13.9219C14.6562 14.3094 14.3406 14.625 13.9531 14.625H2.04688C1.65937 14.625 1.34375 14.3094 1.34375 13.9219V6.73438C1.34375 6.34688 1.65937 6.03125 2.04688 6.03125H13.9531C14.3406 6.03125 14.6562 6.34688 14.6562 6.73438V13.9219Z"
              fill="#5868B0"
            />
            <path
              d="M4.04063 4.1875H4.44063C4.8375 4.1875 5.15625 3.84375 5.15625 3.43125V0.75C5.15625 0.3375 4.8375 0 4.44063 0H4.04063C3.64375 0 3.3125 0.334375 3.3125 0.75V3.43125C3.3125 3.84375 3.64375 4.1875 4.04063 4.1875Z"
              fill="#5868B0"
            />
            <path
              d="M11.8 4.1875H12.2C12.5969 4.1875 12.9062 3.84375 12.9062 3.43125V0.746875C12.9062 0.334375 12.5969 0 12.2 0H11.8C11.4031 0 11.0938 0.334375 11.0938 0.746875V3.42812C11.0938 3.84375 11.4031 4.1875 11.8 4.1875Z"
              fill="#5868B0"
            />
          </svg>
        </div>
        <div className="pay-wrap">
          <div className="pay-label d-flex flex-wrap align-items-center justify-content-between">
            <span>Payment method:</span>
            <img src={paypalImg} alt="img" />
          </div>
        </div>
        <div className="login-btn-wrap text-center">
          {/* <button className="btn btn-default"> */}
          <PaypalExpressBtn client={Client} currency={"INR"} total={1.0}/>
  {/* proceed
  </button> */}
        </div>
      </form>
    </div>
  </div>
);
  }
}
export default BoostContent