import React from "react";
import lappyImg from "../assets/img/lappy.png";
import {headerBanner} from '../actions/BannerAction';
import { useDispatch, useSelector } from 'react-redux'


const Banner = (props) => {
  const [showbanner,setShowbanner] = React.useState(true)
  const  dispatch = useDispatch();
  const headerbanner  = useSelector(s=>s.BannerList.headerbanner)

  React.useEffect(()=>{
    dispatch(headerBanner())
  },[])

  const removeBanner = (e) => {
    setShowbanner(false)
  }
  return (
    showbanner ?
    <div className="banner">
      <div className="screen-lappy">
        <img src={lappyImg} alt="img" />
      </div>
      <h1>{headerbanner.title}</h1>
      <a href={headerbanner.link} className="btn btn-regular" target="_blank">
        see here
      </a>
      <button className="btn-close" onClick={removeBanner}>
        <svg
          width="18"
          height="19"
          viewBox="0 0 18 19"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M10.3411 9.56996L17.6941 2.21309C18.0811 1.83894 18.0811 1.22438 17.6941 0.848944C17.3264 0.472229 16.7196 0.472229 16.339 0.848944L8.98598 8.20324L1.63298 0.848942C1.25884 0.472228 0.646843 0.472228 0.264982 0.848942C-0.109161 1.22437 -0.109161 1.83894 0.264982 2.21309L7.61798 9.56996L0.264982 16.9191C-0.109161 17.2971 -0.109161 17.9104 0.264982 18.2858C0.447532 18.4748 0.696979 18.5674 0.946391 18.5674C1.19711 18.5674 1.44012 18.4748 1.62784 18.2858L8.98598 10.9328L16.339 18.2858C16.5267 18.4748 16.7761 18.5674 17.0127 18.5674C17.2699 18.5674 17.5128 18.4748 17.6941 18.2858C18.0811 17.9104 18.0811 17.2971 17.6941 16.9191L10.3411 9.56996Z"
            fill="#D8D6D6"
          />
        </svg>
      </button>
    </div>:null
  );
}

export default Banner;
