import ReduxType from '../constants/reduxtype'
import { SERVER,ENDPOINTS,HEADERS } from '../apiEndPoints'

export const getallclick = () => {
    return dispatch => {
        return fetch(`${SERVER}${ENDPOINTS.GET_ALL_CLICK}`)
          .then(res=>res.json())
          .then(response=>{
            console.log(response)
            dispatch({ type: ReduxType.GET_ALL_CLICK,payload:response.data})
          })
          .catch(e=>{
            console.log(e)
          })
    }
}