import ReduxType from '../constants/reduxtype'
import { SERVER,ENDPOINTS,HEADERS } from '../apiEndPoints'
import LoadingAction from './LoadingAction'
import Cookies from 'js-cookie'
import Axios from 'axios';

const headers =  new Headers();

const showBookMarkList =  (data,page)  => async dispatch =>  {
  await fetch(`${SERVER}${ENDPOINTS.BOOKMARK_LIST}?user_id=${data}&page=${page}`, {
     method: 'GET',
     'Access-Control-Allow-Origin': '*'
  })
  .then(res=>res.json())
  .then(response=>{
      if(response.status === "success") {
        dispatch({ type: ReduxType.BOOKMARK_LIST_SUCCESS,
                  payload:response})
      } else {
        dispatch({ type: ReduxType.BOOKMARK_LIST_FAIL,
                  payload:response })
      }

  })
  .catch(e=>{
    dispatch({ type: ReduxType.BOOKMARK_LIST_FAIL,
               payload:e
    })
  })

}

export default showBookMarkList;
