import ReduxType from '../constants/reduxtype'
function BookMarkPostsReducer(state = false, { type, payload }) {
  switch (type) {
    case ReduxType.BOOKMARK_POSTS_SUCCESS:
      return payload;
    case ReduxType.BOOKMARK_POSTS_FAIL:
      return payload
    default:
      return state;
  }
}

export default BookMarkPostsReducer;
