import ReduxType from '../constants/reduxtype'
import { SERVER,ENDPOINTS,HEADERS } from '../apiEndPoints'
import LoadingAction from './LoadingAction'
import Cookies from 'js-cookie'


export const headerBanner = (payload) => {
    return dispatch => {
        return fetch(`${SERVER}${ENDPOINTS.HEADERBANNER}`)
          .then(res=>res.json())
          .then(response=>{
            dispatch({ type: ReduxType.GET_HEADER_BANNER,payload:response.data})
          })
          .catch(e=>{
            console.log(e)
          })
    }
}

export const getBannerList = (payload) => {
    return dispatch => {
        return fetch(`${SERVER}${ENDPOINTS.BANNERLIST}`)
          .then(res=>res.json())
          .then(response=>{
            dispatch({ type: ReduxType.BANNER_LIST,payload:response.data})
          })
          .catch(e=>{
            console.log(e)
          })
    }
}

export const bannerAddView = (bannerId,data) => {
    return dispatch => {
        return fetch(`${SERVER}${ENDPOINTS.BANNER_ADD_VIEW}/${bannerId}`,{
          method: 'POST',
          "Access-Control-Allow-Origin": "*"
        })
          .then(res=>res.json())
          .then(response=>{
            data(response.data)
          })
          .catch(e=>{
            console.log(e)
            data(0)
          })
    }
}


