import ReduxType from '../constants/reduxtype'
const initialState = {
    banner: '' ,
    headerbanner : []
}
function FetchAllPostsReducer(state = initialState, { type, payload }) {
	switch (type) {
		case ReduxType.BANNER_LIST:
		  	return {...state,banner:payload};
			case ReduxType.GET_HEADER_BANNER:
				return {...state,headerbanner:payload};
		default:
		  return state;
}
}

export default FetchAllPostsReducer;
