import ReduxType from '../constants/reduxtype'
function BookMarkListReducer(state = false, { type, payload }) {
  switch (type) {
    case ReduxType.BOOKMARK_LIST_SUCCESS:
      return payload;
    case ReduxType.BOOKMARK_LIST_FAIL:
      return payload
    default:
      return state;
  }
}

export default BookMarkListReducer;
