import ReduxType from '../constants/reduxtype'
function PublishedPostsReducer(state = false, { type, payload }) {
  switch (type) {
    case ReduxType.PUBLISHED_POSTS_SUCCESS:
      return payload;
    case ReduxType.PUBLISHED_POSTS_FAIL:
      return payload
    default:
      return state;
  }
}

export default PublishedPostsReducer;
