import ReduxType from '../constants/reduxtype'
import { SERVER,ENDPOINTS,HEADERS } from '../apiEndPoints'
import LoadingAction from './LoadingAction'
import Cookies from 'js-cookie'

const headers =  new Headers();
// headers.append('Access-Control-Allow-Credentials', 'true');
headers.append('Authorization', 'Bearer ' + Cookies.get('token'));
headers.append('Access-Control-Allow-Origin', '*');
headers.append('Access-Control-Allow-Credentials', 'true');

const showBookMarkClicks =  data  => async dispatch =>  {

  await fetch(`${SERVER}${ENDPOINTS.BOOK_MARK_CLICKS}`, {
     body: data,
     method: 'POST',
     headers:headers

  })
  .then(res=>res.json())
  .then(response=>{
      if(response.status === "success") {
        dispatch({ type: ReduxType.BOOKMARK_CLICKS_SUCCESS,
                  payload:response})
      } else {
        dispatch({ type: ReduxType.BOOKMARK_CLICKS_FAIL,
                  payload:response })
      }

  })
  .catch(e=>{
    dispatch({ type: ReduxType.BOOKMARK_CLICKS_FAIL,
               payload:e
    })
  })

}

export default showBookMarkClicks;
