import ReduxType from '../constants/reduxtype'
function UnderReviewPostsReducer(state = false, { type, payload }) {
  switch (type) {
    case ReduxType.UNDER_REVIEW_POSTS_SUCCESS:
      return payload;
    case ReduxType.UNDER_REVIEW_POSTS_FAIL:
      return payload
    default:
      return state;
  }
}

export default UnderReviewPostsReducer;
