import ReduxType from '../constants/reduxtype'
function BookMarkClicksReducer(state = false, { type, payload }) {
  switch (type) {
    case ReduxType.BOOKMARK_CLICKS_SUCCESS:
      return payload;
    case ReduxType.BOOKMARK_CLICKS_FAIL:
      return payload
    default:
      return state;
  }
}

export default BookMarkClicksReducer;
